import React from 'react'

export default class Style extends React.Component {
  render() {
    const { theme } = this.props
    return (
      <style
        dangerouslySetInnerHTML={{
          __html: `
          /**
           * GHColors theme by Avi Aryan (http://aviaryan.in)
           * Inspired by Github syntax coloring
           */
          
          code[class*='language-'],
          pre[class*='language-'] {
            color: #393a34;
            font-family: 'Consolas', 'Bitstream Vera Sans Mono', 'Courier New', Courier,
              monospace;
            direction: ltr;
            text-align: left;
            white-space: pre;
            word-spacing: normal;
            word-break: normal;
            font-size: 0.95em;
            line-height: 1.2em;
          
            -moz-tab-size: 4;
            -o-tab-size: 4;
            tab-size: 4;
          
            -webkit-hyphens: none;
            -moz-hyphens: none;
            -ms-hyphens: none;
            hyphens: none;
          }
          
          pre[class*='language-']::-moz-selection,
          pre[class*='language-'] ::-moz-selection,
          code[class*='language-']::-moz-selection,
          code[class*='language-'] ::-moz-selection {
            background: #b3d4fc;
          }
          
          pre[class*='language-']::selection,
          pre[class*='language-'] ::selection,
          code[class*='language-']::selection,
          code[class*='language-'] ::selection {
            background: #b3d4fc;
          }
          
          /* Code blocks */
          pre[class*='language-'] {
            padding: 1em;
            margin: 0.5em 0;
            overflow: auto;
            border: 1px solid #dddddd;
            background-color: white;
          }
          
          :not(pre) > code[class*='language-'],
          pre[class*='language-'] {
          }
          
          /* Inline code */
          :not(pre) > code[class*='language-'] {
            padding: 0.2em;
            padding-top: 1px;
            padding-bottom: 1px;
            background: ${theme.inlineCode.background};
            color: ${theme.inlineCode.color};
            border: 1px solid #e1e1e8;
            border-radius: 5px;
          }
          
          .token.comment,
          .token.prolog,
          .token.doctype,
          .token.cdata {
            color: #999988;
            font-style: italic;
          }
          
          .token.namespace {
            opacity: 0.7;
          }
          
          .token.string,
          .token.attr-value {
            color: #e3116c;
          }
          .token.punctuation,
          .token.operator {
            color: #393a34; /* no highlight */
          }
          
          .token.entity,
          .token.url,
          .token.symbol,
          .token.number,
          .token.boolean,
          .token.variable,
          .token.constant,
          .token.property,
          .token.regex {
            color: #36acaa;
          }
          
          .token.inserted {
            color: green;
          }
          
          .token.atrule,
          .token.keyword,
          .token.attr-name,
          .language-autohotkey .token.selector {
            color: #00a4db;
          }
          
          .token.function,
          .token.deleted,
          .language-autohotkey .token.tag {
            color: #9a050f;
          }
          
          .token.tag,
          .token.selector,
          .language-autohotkey .token.keyword {
            color: #00009f;
          }
          
          .token.important,
          .token.function,
          .token.bold {
            font-weight: bold;
          }
          
          .token.italic {
            font-style: italic;
          }
          
          /* custom */
          
          .gatsby-highlight-code-line {
            background-color: rgba(240, 240, 240, 0.486);
            display: block;
            margin-right: -1em;
            margin-left: -1em;
            padding-right: 1em;
            padding-left: 0.75em;
            border-left: 0.25em solid #00c3ff;
          }
          
          .gatsby-highlight {
            margin-bottom: 1.75rem;
            border-radius: 10px;
            -webkit-overflow-scrolling: touch;
            overflow: auto;
          }
          
          .gatsby-highlight pre[class*='language-'] {
            float: left;
            min-width: 100%;
          }
          
        `,
        }}
      />
    )
  }
}
